ay: block;\r\n    width: 100%;\r\n    height: 1px;\r\n    margin: 6px 0;\r\n    border-bottom: 1px solid #E0E0E0;\r\n}\r\n\r\n.cnx_insights,\r\n.cnx_ins__sidebar {\r\n    height: calc(100vh - 65px);\r\n}\r\n\r\n.cnx__ins_dashboard {\r\n    height: calc(100vh - 65px);\r\n}\r\n\r\n.animate-pulse {\r\n    animation: pulse 2s cubic-bezier(0.4, 0, 0.6, 1) infinite;\r\n}\r\n\r\n@keyframes pulse {\r\n    0%,\r\n    100% {\r\n        opacity: 1;\r\n    }\r\n    50% {\r\n        opacity: .5;\r\n    }\r\n}\r\n\r\n/* ui */\r\n\r\n/* Dropdown */\r\n\r\n.dropdown {\r\n    position: relative;\r\n    display: inline-block;\r\n}\r\n\r\n.cnx_dropdown__toggle {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    gap: 0.5rem;\r\n    cursor: pointer;\r\n}\r\n\r\n.cnx_dropdown__menu {\r\n    background-color: #fff;\r\n    border: 1px solid #ccc;\r\n    z-index: 1000;\r\n    border-radius: 6px;\r\n    box-shadow: 0 3px 6px rgba(0,0,0,0.1);\r\n    padding: 10px;\r\n    display: flex;\r\n    flex-direction: column;\r\n    gap: 4px;\r\n    width: -moz-fit-content;\r\n    width: fit-content;\r\n}\r\n\r\n.cnx_dropdown__item {\r\n    padding: 5px 10px;\r\n}\r\n\r\n.cnx_dropdown__item:hover {\r\n    background-color: #f5f5f5;\r\n}\r\n\r\n/* search box */\r\n\r\n.cnx__ins__ui_search {\r\n    position: relative;\r\n    box-sizing: border-box;\r\n    width: 100%;\r\n}\r\n\r\n.cnx__ins__ui_search_input {\r\n    padding: 6px 10px 6px 30px;\r\n    border-radius: 4px;\r\n    border: 1px solid rgba(33,35,44,0.24);\r\n    font-size: 14px;\r\n    font-weight: 500;\r\n    width: 100%;\r\n}\r\n\r\n.cnx__ins__ui_search_input:focus {\r\n    outline: none;\r\n    border-color: rgba(25, 114, 247, 0.48);\r\n}\r\n\r\n.cnx__ins__ui_search_icon {\r\n    position: absolute;\r\n    top: 50%;\r\n    left: 10px;\r\n    transform: translateY(-50%);\r\n    color: rgba(33,35,44,0.24);\r\n}\r\n\r\n.cnx__ins__ui_search_icon_svg {\r\n    width: 14px;\r\n    height: 14px;\r\n}\r\n\r\n.cnx__ins__ui_search_input:focus + .cnx__ins__ui_search_icon > .cnx__ins__ui_search_icon_svg {\r\n    fill: #1972f7;\r\n}\r\n\r\n/* button */\r\n\r\n.cnx__btn {\r\n    display: inline-block;\r\n    padding: 10px 20px;\r\n    font-size: 16px;\r\n    font-weight: 600;\r\n    -webkit-text-decoration: none;\r\n    text-decoration: none;\r\n    transition: all 0.3s ease;\r\n    border: 1px solid #1967f8;\r\n    border-radius: 4px;\r\n    display: flex;\r\n    align-items: center;\r\n    gap: 6px;\r\n    white-space: nowrap;\r\n}\r\n\r\n.cnx__btn > .cnx__btn_icon {\r\n    font-size: 18px;\r\n    padding: 0;\r\n    margin: 0;\r\n}\r\n\r\n.cnx__btn:Hover {\r\n    cursor: pointer;\r\n    background-color: rgba(0,0,0, .8);\r\n    border-color: rgba(0,0,0, .8);\r\n}\r\n\r\n/* button veriant */\r\n\r\n/* primary */\r\n\r\n.cnx__btn_primary {\r\n    background-color: #1967f8;\r\n    border-color: #1967f8;\r\n    color: #fff;\r\n}\r\n\r\n.cnx__btn_primary:hover {\r\n    background-color: rgba(0,0,0, .8);\r\n    border-color: rgba(0,0,0, .8);\r\n}\r\n\r\n.cnx__btn_primary > .cnx__btn_icon {\r\n    color: #fff;\r\n}\r\n\r\n/* secondary */\r\n\r\n.cnx__btn_secondary {\r\n    background-color: #e4e4e4;\r\n    border-color: #e4e4e4;\r\n    color: #000;\r\n}\r\n\r\n.cnx__btn_secondary:hover {\r\n    background-color: rgba(0,0,0, .8);\r\n    color: #fff;\r\n    border-color: rgba(0,0,0, .8);\r\n}\r\n\r\n.cnx__btn_secondary > .cnx__btn_icon {\r\n    color: #000;\r\n}\r\n\r\n/* tertiary */\r\n\r\n.cnx__btn_tertiary {\r\n    background-color: #fff;\r\n    border-color: #dadada;\r\n    color: #383838;\r\n}\r\n\r\n.cnx__btn_tertiary:hover {\r\n    background-color: #f1f1f1;\r\n    border-color: #dadada;\r\n}\r\n\r\n/* success */\r\n\r\n.cnx__btn_success {\r\n    background-color: #01a545;\r\n    border-color: #01a545;\r\n    color: #fff;\r\n}\r\n\r\n.cnx__btn_success:hover {\r\n    background-color: #018839;\r\n    border-color: #018839;\r\n}\r\n\r\n.cnx__btn_tertiary > .cnx__btn_icon {\r\n    color: #383838;\r\n}\r\n\r\n/* button sizes */\r\n\r\n.cnx__btn_lg {\r\n    padding: 15px 30px;\r\n    font-size: 18px;\r\n}\r\n\r\n.cnx__btn_lg > .cnx__btn_icon {\r\n    font-size: 20px;\r\n}\r\n\r\n.cnx__btn_md {\r\n    padding: 10px 20px;\r\n    font-size: 16px;\r\n}\r\n\r\n.cnx__btn_md > .cnx__btn_icon {\r\n    font-size: 16px;\r\n}\r\n\r\n.cnx__btn_sm {\r\n    padding: 5px 10px;\r\n    font-size: 14px;\r\n}\r\n\r\n.cnx__btn_sm > .cnx__btn_icon {\r\n    font-size: 14px;\r\n}\r\n\r\n/* disable */\r\n\r\n.cnx__btn_disabled:disabled {\r\n    background-color: #e4e4e4;\r\n    border-color: #e4e4e4;\r\n    color: #000;\r\n    cursor: not-allowed;\r\n}\r\n\r\n.cnx__btn_success.cnx__btn_disabled:disabled {\r\n    background-color: #3e9763;\r\n    border-color: #01a545;\r\n    color: #fff;\r\n    cursor: not-allowed;\r\n    opacity: 0.5;\r\n}\r\n\r\n/* tooltip */\r\n\r\n.cnx__ins__ui_tooltip {\r\n    z-index: 999;\r\n    background-color: #303030;\r\n    color: #ececec;\r\n    font-size: 12px;\r\n    padding: 5px 10px;\r\n    border-radius: 4px;\r\n}\r\n\r\n/* tooltip arrow */\r\n\r\n.cnx__ins__ui_tooltip_arrow {\r\n    width: 0;\r\n    height: 0;\r\n    border-style: solid;\r\n    border-width: 0 5px 5px 5px;\r\n    border-color: transparent transparent #303030 transparent;\r\n    position: absolute;\r\n    top: -5px;\r\n    left: 50%;\r\n    margin-left: -5px;\r\n}\r\n\r\n.cnx_modal {\r\n    width: 100vw;\r\n    height: 100vh;\r\n    position: fixed;\r\n    top: 0;\r\n    left: 0;\r\n    background-color: rgba(0,0,0,0.2);\r\n    z-index: 999;\r\n}\r\n\r\n/* card */\r\n\r\n.cnx_card {\r\n    width: 100%;\r\n    height: -moz-fit-content;\r\n    height: fit-content;\r\n    background-color: #fff;\r\n    box-shadow: 0 0 10px 0 rgba(0,0,0,0.1);\r\n    box-sizing: border-box;\r\n    position: relative;\r\n}\r\n\r\n.cnx_card__header {\r\n    display: flex;\r\n    align-items: center;\r\n    justify-content: space-between;\r\n    padding: 12px;\r\n    padding-left: 16px